<template>
    <div>
      <div class="modal" v-if="showOptions">
        <div class="modal-content" v-if="showOptions">
          <label for="acc-prev-answers-cb">Keep track of all answers in a round?</label>
          <input class="checkbox" type="checkbox" v-model="doAccumulatePreviousAnswers" id="acc-prev-answers-cb">
          <div class="modal-menu">
            <a href="#" @click="showOptions = false">close</a>
          </div>
        </div>
      </div>
      <div class="modal" v-if="showInstructions">
        <div class="modal-content" v-if="showInstructions">
          <p>
            This is a single player English version of of a Japanese word game called Shiritori with a 
            computer player as an opponent. You take turns with the computer saying words that start 
            with the final sound of the previous word. A round starts when you record any English word 
            and click submit. The round ends when you send a word starting with an incorrect sound or 
            you send a word with a final sound "ng" // as in "song", which has no words that start 
            with it in English (barring a few rare loan words). There is an option to also lose if you 
            say the same answer more than once in round. The score keeps track of the number of times 
            you were correct in the current round.
          </p>
          <p>
            This is based only on the pronunciation of a word, and not the spelling. Vowel groups are 
            also counted as one sound. The sound of "ir" in "bird" and "ear" in "early" is also one 
            sound. Some examples of valid chains of answers:
          </p>
          <div class="example-container">
            <ul>
              <li>bird -> drive -> voyage -> giraffe</li>
              <li>fly -> Idaho -> omen -> new</li>
            </ul>
          </div>
          <p>
            The phonetic transcriptions are all shown in a small subset of the <a href="https://en.wikipedia.org/wiki/International_Phonetic_Alphabet">International Phonetic Alphabet</a>.
          </p>
          <p><a target="_blank" href="https://icons8.com/icon/12653/microphone">Microphone</a> icon by <a target="_blank" href="https://icons8.com">Icons8</a></p>
          <p><a href="https://www.flaticon.com/free-icons/speaker" title="speaker icons">Speaker icons created by Freepik - Flaticon</a></p>
          <div class="modal-menu">
            <a href="#" @click="showInstructions = false">close</a>
          </div>
        </div>
      </div>
      <h1 class="game-title">Hitori Shiritori</h1>
      <div v-if="!hasStarted" class="start-prompt">
        <p>Record any word to get started.</p>
      </div>
      <div v-if="isLoading" class="loader-container">
        <div class="loader"/>
      </div>
      <div v-else class="feedback-container">
      
        <div class="user-answer-container" v-if="userAnswer.length > 0">
            <p>I heard you say: <span class="user-answer">/{{userAnswer}}/</span></p>
        </div>
        <div class="incorrect-msg-container">
          <p v-if="lastInputWasRepeat">You've already said that word before, try again.</p>
          <p v-else-if="lastInputWasWrong">That was incorrect, try again.</p>
        </div>
        <div class="computer-answer-container" v-if="computerWord.length >0 || computerIpa.length > 0">
            <p class="next-word-prefix">The next word is:</p>
            <p>
              <span class="computer-answer-row" @click="playComputerAudio"><span>{{computerWord}}</span> - <span>/{{computerIpa}}/</span>
                <img class="speaker-icon" src="../assets/speaker-filled-audio-tool.png">
              </span>
              <audio ref="computerAudio" v-show="false" v-if="computerAudioUrl" :src="computerAudioUrl"/>
            </p>
        </div>
      
      </div>
      <div class="score-container">
          <p>Score: <span>{{tweenedScore.toFixed(0)}}</span></p>
      </div>
      <div class="btn-container">
          <div class="btn record-btn" 
              @click="handleRecordClick()">
              <img class="recording-center"
                src="../assets/icons8-microphone-50.png" 
                :class="{'recording': isRecording}">
          </div>
      </div>
      <div class="audio-container">
        <audio id="audio" ref="audioPlayer"/>
        <button class="submit-btn" :disabled="hasSubmittedOnce" v-show="audio" @click="callEvaluatorApi">Submit</button>
      </div>
      <div class="menu-container">
        <div>
          <a href="#" @click="showInstructions = true">How to play</a>
        </div>
        <div>
          <a href="#" @click="showOptions = true">Options</a>
        </div>
      </div>
      <div>
        <p>Submitted audio is saved in order to further train and improve the model.</p>
      </div>
    </div>
</template>
<script>
import RecordRTC from "../RecordRTC";
import gsap from 'gsap';
import { nextTick } from '@vue/runtime-core';
const axios = require("axios");
const API_URL = 'https://tjrg921f64.execute-api.ap-northeast-1.amazonaws.com/Prod/hitori_shiritori/';
export default {
    data: function () {
        return {
            score: 0,
            recorder: null,
            audio: null,
            computerAudioUrl: '',
            hasSubmittedOnce: false,
            userAnswer: '',
            computerWord: '',
            computerIpa: '',
            computerPromptLastSound: '',
            isLoading: false,
            isRecording: false,
            doAccumulatePreviousAnswers: false,
            previousUserAnswers: [],
            testCounter: 0,
            tweenedScore: 0,
            hasStarted: false,
            showOptions: false,
            showInstructions: false,
            lastInputWasWrong: false,
            lastInputWasRepeat: false,
        }
    },
    methods: {

      stopRecording() {
        if(this.isRecording) {
          this.recorder.stopRecording(this.stopRecordingCallback);
          this.isRecording = false;
        }
      },
      handleRecordClick() {
        if (!this.isRecording) {
          this.hasSubmittedOnce = false;
          this.isRecording = true;
          this.onRecordClick();
          setTimeout(() => {
            this.stopRecording();
          }, 2000)
        } else {
          this.stopRecording();
        }
      },
      getIsIncorrectBasedOnPreviousAnswer(newAnswer) {
        if(this.doAccumulatePreviousAnswers) {
          return this.previousUserAnswers.includes(newAnswer);
        } else {
          return false;
        }
      },
      async playComputerAudio() {
        nextTick(() => {
          if(this.$refs.computerAudio) {
            this.$refs.computerAudio.play();
          }
        })
      },
      handleEvaluatorResponseData(data) {
          if(data.hasError) {
            alert("User input not recognized, please try again.");
            return;
          }
          if(!this.hasStarted) this.hasStarted = true;
          let isIncorrectBasedOnPreviousAnswer = false;
          if(data.userAnswer) {
            isIncorrectBasedOnPreviousAnswer = this.getIsIncorrectBasedOnPreviousAnswer(data.userAnswer);
            this.userAnswer = data.userAnswer;
            if(this.doAccumulatePreviousAnswers) {
              this.previousUserAnswers.push(this.userAnswer)
            }
          }
          if(data.isCorrect && !isIncorrectBasedOnPreviousAnswer) {
            this.score += 1;
            this.lastInputWasWrong = false;
            this.lastInputWasRepeat = false;
          }else {
            this.score = 0;
            this.lastInputWasWrong = true;
            this.lastInputWasRepeat = isIncorrectBasedOnPreviousAnswer;
          }
          this.computerWord = data.computerWord;
          this.computerIpa = data.computerIpa;
          this.computerPromptLastSound = data.computerPromptLastSound;
          this.computerAudioUrl = data.computerAudio;
          this.playComputerAudio();
          
      },
      async callEvaluatorApi(){
        if(!this.hasSubmittedOnce) {
          this.hasSubmittedOnce = true;
          let formData = new FormData();
          formData.set("audio", this.audio);
          formData.append("prevPrompt", this.computerPromptLastSound);
          this.isLoading = true;
          try {
              let response = await axios({
                  method: 'post',
                  url: API_URL,
                  data: formData,
                  timeout: 30000,
                  headers: {
                      "access-control-allow-origin": '*',
                      'content-type': 'multipart/form-data'
                  }
              });
              this.isLoading = false;
              this.handleEvaluatorResponseData(response.data);
          } catch (err){
              this.isLoading = false;
              console.log(err);
              this.hasSubmittedOnce = false;
          }
        }
      },
      stopRecordingCallback() {
        var blob = this.recorder.getBlob();
        this.audio = blob;
        this.$refs.audioPlayer.srcObject = null;
        this.$refs.audioPlayer.src = URL.createObjectURL(blob);
        this.$refs.audioPlayer.controls = true;
        this.$refs.audioPlayer.setAttribute("style", "display:inherit");
        this.recorder.microphone.stop();
      },
      onRecordClick() {
        let that = this;
        this.captureMicrophone(function(microphone) {
          that.$refs.audioPlayer.srcObject = microphone;

          that.recorder = RecordRTC(microphone, {
            type: "audio",
            recorderType: RecordRTC.StereoAudioRecorder,
            numberOfAudioChannels: 1,
            desiredSampRate: 16000
          });
  
          that.recorder.startRecording();
  
          // release microphone on stopRecording
          that.recorder.microphone = microphone;
        });
      },
      captureMicrophone(callback) {
        navigator.mediaDevices
          .getUserMedia({
            audio: true
          })
          .then(function(microphone) {
            callback(microphone);
          })
          .catch(function(error) {
            alert(
              "Unable to capture your microphone. Please check console log."
            );
            console.error(error);
          });
      },
      mounted: function () {

      }
    },
    watch: {
      score(newVal, oldVal) {
        if(newVal < oldVal) {
          gsap.to(this, { duration: 0.5, tweenedScore: Number(newVal) || 0 })
        }
        else {
          this.tweenedScore = newVal;
        }
      }
    }
}
</script>
